          return this.users[this.user].comment ? this.users[this.user].comment : ''; 
        },
        set: function(value) {
            this.comment = value;
        }
      },
      question1: function() {
        let _this = this;

        if (this.users[this.user].q1) {
          _this.q1 = this.users[this.user].q1
        }
        return _this.q1;
      },
      question2: function() {
        let _this = this;

        if (this.users[this.user].q2) {
          _this.q2 = this.users[this.user].q2
        }
