          _this.q12 = this.users[this.user].q12
        }
        return _this.q12;
      },
    },
    methods: {
      changeQ1: function(num){
        this.q1[num] = !this.q1[num];
      },
      changeQ2: function(num){
        this.q2[num] = !this.q2[num];
      },
      changeQ3: function(num){
        this.q3[num] = !this.q3[num];
      },
      changeQ4: function(num){
        this.q4[num] = !this.q4[num];
      },
      changeQ5: function(num){
        this.q5[num] = !this.q5[num];
      },
      changeQ10: function(num){
        this.q10[num] = !this.q10[num];
      },
      changeQ11: function(num){
        this.q11[num] = !this.q11[num];
      },
      changeQ12: function(num){
        if(num !== 'r6') {
          this.q12[num] = !this.q12[num] ;
        } else {
          this.q12[num] = this.inputText;
        }
      },
      update() {
        this.users[this.user].q1 = this.q1;
        this.users[this.user].q2 = this.q2;
        this.users[this.user].q3 = this.q3;
        this.users[this.user].q4 = this.q4;
