        this.users[this.user].q11 = this.q11;
        this.users[this.user].q12 = this.q12;
        this.users[this.user].comment = this.comment;

        window.localStorage.setItem('users', JSON.stringify(this.users)); 
        this.clickTable();
      }
    }
}
