<template>
    <div>
    <div class="titles">
        <div class="title title-left">
            à LA DEMANDE
        </div>
        <div class="title title-right">
            {{service}}
        </div>
    </div>
    <div class="graphs">
        <GraphLine v-bind:years="demandCostSimulationValue" v-bind:color="'first'" v-bind:isDasharay="false"/>
        <GraphLine v-bind:years="demandSystemAvValue" v-bind:color="'second'" v-bind:isDasharay="true"/>
        <GraphLine v-bind:years="currentCostSimulationValue" v-bind:color="'theard'" v-bind:isDasharay="false"/>
        <GraphLine v-bind:years="currentSystemAvValue" v-bind:color="'fourth'" v-bind:isDasharay="true"/>
    </div>
    <div class="graphs-labels">
        <div class="left">
            <div class="label label-first">
                COûT
            </div>
            <div class="label label-second">
                Disponibilité
            </div>
        </div>
        <div class="right">
            <div class="label label-theard">
                COûT
            </div>
            <div class="label label-fourth">
                Disponibilité
            </div>
        </div>
     </div>
    </div>
</template>

<script>
import GraphLine from './GraphLine.vue';

export default {
    name: 'GraphsLines',
    components: {
        GraphLine
    },
    data: ()=>({
        demandData: [],
        currentData: [],
        activeGraph: []

    }),

    created: function(){
        this.getDemandeGraph();
        this.getCurrentGraph();
    },
    computed: {
        service(){
            return this.$store.state.service;
        },
        year: function() {
            return this.$store.state.currentYear;
        },
        graph: function(){
            return this.$store.state.simulatorGraph;
        },
        demandCostSimulationObject: function(){
            return this.demandData[0];
        },
        demandCostSimulationValue: function(){
            var coordinates = [];
            if(this.demandCostSimulationObject){
                for (var key in this.demandCostSimulationObject) {
                    if(key.charAt(0) == 'y'){
                        coordinates.push(this.demandCostSimulationObject[key]);
                    }
                }
            }
            return coordinates;
        },
        demandSystemAvObject: function(){
            return this.demandData[2];
        },
        demandSystemAvValue: function(){
            var coordinates = [];
            if(this.demandSystemAvObject){
                for (var key in this.demandSystemAvObject) {
                    if(key.charAt(0) == 'y'){
                        coordinates.push(this.demandSystemAvObject[key]);
                    }
                }
            }
            return coordinates;
        },
        currentGraph: function(){
            return this.$store.state.simulatorGraph;
        },
        currentGraphTitle: function(){
            if(this.$store.state.simulatorGraph == 'packaged-spare' || this.$store.state.simulatorGraph == 'packaged-upgrade'){
                return 'Niveau 1 : Basique';
            } else if (this.$store.state.simulatorGraph == 'managed') {
                return 'Niveau 3 : Avancé';
            } else if (this.$store.state.simulatorGraph == 'supported'){
                return 'Niveau 2 : Intermédiaire';
            }
        },
        currentCostSimulationObject: function(){
            return this.currentData[0];
        },
        currentCostSimulationValue: function(){
            var coordinates = [];
            if(this.currentCostSimulationObject){
                for (var key in this.currentCostSimulationObject) {
                    if(key.charAt(0) == 'y'){
                        coordinates.push(this.currentCostSimulationObject[key]);
                    }
                }
            }
            return coordinates;
        },
        currentSystemAvObject: function(){
            return this.currentData[2];
        },
        currentSystemAvValue: function(){
            var coordinates = [];
            if(this.currentSystemAvObject){
                for (var key in this.currentSystemAvObject) {
                    if(key.charAt(0) == 'y'){
                        coordinates.push(this.currentSystemAvObject[key]);
                    }
                }
            }
            return coordinates;
        },
    },
    methods: {
         getDemandeGraph: function(){
            var data = window.localStorage.getItem('ondemand');
            this.demandData =  JSON.parse(data);
        },
        getCurrentGraph: function(){
            var data = window.localStorage.getItem(this.currentGraph)
            this.currentData = JSON.parse(data);
        }
    }
}
</script>

