<template>
  <div id="app">
    <PopupToHome v-if="isPopup"/>
    <SecretDoor v-if="currentPage === 'secret'" />
    <Header v-if="currentPage !== 'home'"/>
    <Home v-if="currentPage === 'home'"/>
    <Slider v-if="currentPage === 'slider'"/>
    <Bibliotheque v-if="currentPage === 'bibliotheque'"/>
    <Contacts v-if="currentPage === 'contacts'"/>
    <Quiz v-if="currentPage === 'quiz'"/>
    <Simulateur v-if="currentPage === 'simulateur'"/>
    <Services v-if="currentPage === 'services'"/>
    <Products v-if="currentPage === 'products'" />
    <QuizPopup v-if="isQuizPopup" />
    <DonePopup v-if="isDonePopup" />
    <Navigation v-if="currentPage !== 'slider' && currentPage !== 'home' && currentPage !== 'services' && currentPage !== 'products'"/>
  </div>
</template>

<script>
import Bibliotheque from './components/Bibliotheque/Bibliotheque.vue';
import Contacts from './components/Contacts/Contacts.vue'; 
import Header from './components/Header/Header.vue';
import Home from './components/Home/Home.vue';
import Navigation from './components/Navigation/Navigation.vue';
import PopupToHome from './components/PopupToHome/PopupToHome.vue';
import Products from './components/Products/Products.vue';
import Quiz from './components/Quiz/Quiz.vue';
import QuizPopup from './components/QuizPopup/QuizPopup.vue';
import SecretDoor from './components/SecretDoor/SecretDoor.vue';
import Services from './components/Services/Services.vue';
import Simulateur from './components/Simulateur/Simulateur.vue';
import Slider from './components/Slider/Slider.vue';
import DonePopup from './components/DonePopup/DonePopup.vue';

export default {
  name: 'app',
  components: {
    Bibliotheque,
    Contacts,
    DonePopup,
    Header,
    Home,
    Navigation,
    PopupToHome,
    Products,
    Quiz,
    QuizPopup,
    SecretDoor,
    Services,
    Simulateur,
    Slider
  },
  computed: {
    isDonePopup(){
      return this.$store.state.isDonePopup;
    },
    currentPage() {
      return this.$store.state.currentPage;
    },
    isPopup() {
      return this.$store.state.isPopupToHome;
    },
    isQuizPopup(){
      return this.$store.state.isQuizPopup;
    }
  }
}
</script>

