<template>
  <div class="slider">
        <Slide v-bind:currentSlider="currentSlider" />
        <div class="slider-to-prew" v-if="currentSlider !== 1">
            <img src="../../assets/arrow-left.png"  @click="prewSlide">
        </div>
        <div class="slider-to-next" v-if="currentSlider !== 3">
            <div class="next-slide-content">
            <img src="../../assets/arrow-right.png" @click="nextSlide">
            </div>
        </div>
        <div class="slider-nav">
            <div class="slider-nav-item" v-bind:class="{active : currentSlider == 1}" @click="firstSlider"></div>
            <div class="slider-nav-item" v-bind:class="{active : currentSlider == 2}" @click="secondSlider"></div>
            <div class="slider-nav-item" v-bind:class="{active : currentSlider == 3}" @click="theardSlider"></div>
        </div>
  </div>
</template>

<script>
import Slide from './Slide.vue';

export default {
    name: 'Slider',

    data: () => ({
        currentSlider: 1
    }),

    components: {
        Slide
    },

    methods: {
        firstSlider: function(){
            this.currentSlider = 1;
        },
        secondSlider: function(){
            this.currentSlider = 2;
        },
        theardSlider: function(){
            this.currentSlider = 3;
        },

        nextSlide: function() {
            if(this.currentSlider == 3) {
                return false;
            }
            this.currentSlider = this.currentSlider + 1;
        },

        prewSlide: function(){
            if(this.currentSlider == 1 ) {
                return false;
            }
            this.currentSlider = this.currentSlider - 1;
        }
    }
}
</script>

