<script>
import GraphsColumns from './GraphsColumns.vue';
import GraphsLines from './GraphsLines.vue';
import TimeLine from './TimeLine.vue';

export default {
    name: 'Simulateur',
    // data: ()=> ({
    //     typeGraph: 'column'
    // }),
    components: {
        GraphsColumns,
        GraphsLines,
        TimeLine
    },
    computed: {
        typeGraph: function() {
            return this.$store.state.graphType;
        },
        simulatorStep: function() {
            return this.$store.state.simulatorStep;
        },
        year: function() {
            return this.$store.state.currentYear;
        },
        graph: function(){
            return this.$store.state.simulatorGraph;
        },
        currentData: function () {
            var data = window.localStorage.getItem(this.graph)
            var currentData = JSON.parse(data);
            return currentData;
        },
        currentSpareColor: function () {
            if(this.currentData) {
                let colors = this.currentData[3];
                return colors[this.year];
            }
        },
        currentRepairColor: function () {
            if(this.currentData) {
                let colors = this.currentData[4];
                return colors[this.year];
            }
        },
        currentTechnicalColor: function () {
            if(this.currentData) {
                let colors = this.currentData[5];
                return colors[this.year];
            }
        },
        currentUpgradeColor: function () {
            if(this.currentData) {
                let colors = this.currentData[6];
                return colors[this.year];
            }
        },
        currentRadarContent: function () {
            if(this.currentData) {
                let colors = this.currentData[9];
                this.messageSimulateurChanged();
                return colors[this.year];
            }
        },
    },
    methods: {
        changeTypeGraph: function() {
            if(this.$store.state.graphType == 'column') {
                this.$store.state.graphType = 'line';
            } else {
                this.$store.state.graphType = 'column';
            }
        },
        messageSimulateurChanged: function() {
            if(this.currentData[9][this.year]) {
                this.$store.state.messageSimulateur = true
            } else {
                this.$store.state.messageSimulateur = false
            }
        },
        upgrade: function() {
            if(this.$store.state.simulatorGraph == 'packaged-spare' || this.$store.state.simulatorGraph == 'packaged-upgrade') {
                this.$store.state.simulatorGraph = 'packaged-upgrade';
            }
        },
        spare: function() {
            if(this.$store.state.simulatorGraph == 'packaged-spare' || this.$store.state.simulatorGraph == 'packaged-upgrade') {
                this.$store.state.simulatorGraph = 'packaged-spare';    
            }
        }

    }
    
}
</script>

