<template>
    <div class="popup-container">
        <div class="popup-message">
            Upgrade effectué !
        </div>
    </div>
</template>

<script>

export default {
    name: 'DonePopup',
    data: ()=> ({
        timer: null
    }),
    created: function(){
        this.createTimer();
    },
    destroyed: function(){
        this.clearTimerDestroy();
    },
    methods: {
        timerFunc(){
            this.$store.state.isDonePopup = false;
            clearTimeout(this.timer);
            this.timer = null;
        },
        createTimer(){
            this.timer = window.setTimeout(this.timerFunc.bind(this), 1000);
        },
        clearTimerDestroy(){
            clearTimeout(this.timer);
            this.timer = null;
        }
    }
}
</script>

