<template>
    <div class="graph-col" v-bind:class="color" :style="styleHeight">
        <div class="label" :style="styleColor">{{label}}</div>
    </div>
</template>

<script>

export default {
    name: 'GraphColumn',
    props: {
        color: String,
        label: String,
        size: String
    },
    computed: {
        styleHeight: function(){
            var height = +this.size * 150;
            return 'height: ' + height + 'px;';
        },
        styleColor: function(){
            var currentColor;
            if(this.color == 'first') {
                currentColor = '#7d9aef';
            } else if(this.color == 'second'){
                currentColor = '#a6b9f0';
            } else if (this.color == 'theard') {
                currentColor = '#5dbfd4';
            } else if (this.color == 'fourd') {
                currentColor = '#a4dce8';
            }
            return 'color: ' + currentColor + ';' ;
        }
    }

}
</script>

