<template>
<div>
    <div class="home">
        <Background />
        <div class="circle">
                <div class="title">
                    <img src="../../assets/header-logo.png" class="thales-logo">
                </div>
                <div class="main-title">
                    <img class="main-logo" src="../../assets/logotype_APP3.png">
                </div>
                <div class="subtitle white">Notre démonstrateur</div>
                <div class="subtitle">Pérennité des systèmes</div>
                <div class="description">
                    <vue-typed-js
                        :strings="['Imaginez votre système toujours disponible', ' Imaginez votre système répondant toujours à votre besoin', 'Imaginez votre système pérenne', 'Imaginez votre système avec un coût de possession maitrisé']"
                        :typeSpeed="100"
                        :loop="true"
                    >
                    <span class="typing"></span>
                    </vue-typed-js>
                </div>
                <div class="button" v-bind:class="{'button-active': buttonActive}" @click="showSlider">
                    Démarrer
                </div>
                <div class="circle-loader">
                    <img src="../../assets/radar.png" class="circle-loader-img" >
                </div>
        </div>
    </div>
</div>
</template>

<script>
import Background from './Background.vue';
import { VueTypedJs } from 'vue-typed-js';
import  manageservices from '../Data/manageservices.js';
import ondemand from '../Data/ondemand.js';
import stock from '../Data/stock.js';
import support from '../Data/support.js';
import upgrade from '../Data/upgrade.js';

export default {
    name: 'Home',
    components: {
        Background,
        VueTypedJs
    },
    data: () => ({
        count: 1,
        buttonActive: false,
        phrases: ['toujours disponible', 'répondant toujours à votre besoin', 'pérenne', 'avec un coût de possession maitrisé']
    }),
    beforeCreate:{
        currentStep : {
            get : function(){
                return this.count;
            },
            set : function(v){
                this.count =  v;
            }
        },
    },
    created : function(){
        this.changeStep();
        this.getAllDataInLocalStorage('ondemand', ondemand);  
        this.getAllDataInLocalStorage('managed', manageservices);  
        this.getAllDataInLocalStorage('packaged-spare', stock);  
        this.getAllDataInLocalStorage('packaged-upgrade', upgrade);  
        this.getAllDataInLocalStorage('supported', support);  
        setTimeout(function activeBtn (){
            this.buttonActive = true;
        }.bind(this), 6000); 
    },
    beforeDestroy () {
       clearInterval(this.count)
       clearInterval(this.currentStep)
    },
    methods : {
        changeStep : function(){          
            this.currentStep = setInterval(function(){
                if(this.count == 6) {
                    this.count = 0;
                    this.currentStep = 0;
                }
                this.count++
                this.currentStep = this.count;
                return this.currentStep;
            }.bind(this), 3000);
        },
        showSlider: function(){
            this.$store.state.currentPage = "slider"  
        },
        getAllDataInLocalStorage(item, data) {
            window.localStorage.setItem(item, JSON.stringify(data));
        }
    },
    computed: {
        background: function () {
            return this.backgrounds[this.count-1];
        }
    }
}

</script>

