<template>
  <div class="preload">
      <div class="label">Le <span class="bolder">Quiz</span></div>
      <div class="animate">
          {{symbol}}
          <div class="radar">
              <img src="../../assets/small-radar.png">
          </div>
      </div>
      <div class="title">{{title}}</div>
      <div class="start-btn" @click="startTest">C'est parti !</div>
  </div>
</template>

<script>

export default {
    name: 'Preload',
    data: () => ({
        preloadStep: 0,
        symbols: ["6", "30", "4", "1", "?"],
        titles: ["questions", "secondes par question", "réponses possibles", "seule correcte", "À vous de jouer !"]
    }),
    beforeCreate:{
        step : {
            get : function(){
                return this.preloadStep;
            },
            set : function(v){
                this.preloadStep =  v;
            }
        }
    },
    created : function(){
        this.changeStep()     
    },
    beforeDestroy () {
       clearInterval(this.step)
       clearInterval(this.preloadStep)
    },
    methods : {
        changeStep : function(){          
            this.step = setInterval(function(){
                if(this.preloadStep == 4) {
                    this.preloadStep = 0;
                    this.step = 0;
                    return this.step;
                } else {
                    this.preloadStep++
                    this.step = this.preloadStep;
                    return this.step;
                }
            }.bind(this), 3000);
        },
        startTest: function(){
            this.$store.state.quizState = "test";
        }
    },
    computed: {
        title: function () {
            return this.titles[this.preloadStep];
        },   
        symbol: function () {
            return this.symbols[this.preloadStep];
        }
    }
}
</script>

