<template>
  <div>
      <Table v-if="component == 'table'" :users="users" :clickRow="showMoreInfo"/>
      <MoreInfo v-if="component == 'moreInfo'" :user="user" :users="users" :clickTable="showTable"/>
  </div>
</template>

<script>
import MoreInfo from './MoreInfo.vue';
import Table from './Table.vue';

export default {
    name: 'SecretDoor',
    data: () => ({
        users: [],
        userId: 0
    }),
    computed: {
        component: function(){
            return this.$store.state.currentSecretComponent;
        },
        user: function(){
            return this.userId;
        }
    },
    components: {
        MoreInfo,
        Table
    },
    created(){
        this.getAllUSers();
    },
    methods: {
        getAllUSers() {
            var data = window.localStorage.getItem('users');
            this.users = JSON.parse(data); 
        },
        showMoreInfo: function(id) {
            this.userId = id;
            this.$store.state.currentSecretComponent = 'moreInfo';
        },
        showTable: function(){
            this.$store.state.currentSecretComponent = 'table';
        }
    }
}
</script>

