<template>
  <div class="test-window">
     <div class="label">Le <span class="bolder">Quiz</span></div>
     <Stepper />
    
    <div class="title">
        {{currentQuestion.question}}
    </div>
    <div class="variants">
        <div class="variant" @click="addAnswer(1)" v-bind:class="{'activeans' : answer == 1}">
            {{currentQuestion.variant_1}}
        </div>
        <div class="variant" @click="addAnswer(2)" v-bind:class="{'activeans' : answer == 2}">
            {{currentQuestion.variant_2}}
        </div>
        <div class="variant" @click="addAnswer(3)" v-bind:class="{'activeans' : answer == 3}">
            {{currentQuestion.variant_3}}
        </div>
        <div class="variant" @click="addAnswer(4)" v-bind:class="{'activeans' : answer == 4}">
            {{currentQuestion.variant_4}}
        </div>
    </div>
    <Timer  v-if="currentTestStep == 0 && !isResultAnswerWindow" key="step0" :endTime="endTime"/>
    <Timer v-if="currentTestStep == 1 && !isResultAnswerWindow" key="step1" :endTime="endTime"/>
    <Timer  v-if="currentTestStep == 2 && !isResultAnswerWindow" key="step2" :endTime="endTime"/>
    <Timer  v-if="currentTestStep == 3 && !isResultAnswerWindow" key="step3" :endTime="endTime"/>
    <Timer  v-if="currentTestStep == 4 && !isResultAnswerWindow" key="step4" :endTime="endTime"/>
    <Timer v-if="currentTestStep == 5 && !isResultAnswerWindow" key="step5" :endTime="endTime"/>
    <AnswerResult v-if="isResultAnswerWindow" :result="resultAnswer" :nextStep="nextStep" :step="currentTestStep" />
    <AnswerTimeEnd v-if="isTimeEnd" :nextStep="nextStep" :step="currentTestStep" />
    <TestResult v-if="isTestEnd"  :result="testResult" />
  </div>
</template>

<script>
import AnswerTimeEnd from './AnswerTimeEnd.vue';
import AnswerResult from './AnswerResult.vue';
import Stepper from './Stepper.vue';
import TestResult from './TestResult.vue';
import Timer from './Timer.vue';

export default {
    name: 'Test',
    components: {
        AnswerTimeEnd,
        AnswerResult,
        Stepper,
        TestResult,
        Timer
    },
    data: () => ({
        answer: 0,
        isResultAnswerWindow : false,
        resultAnswer:'',
        isTestEnd: false,
        isTimeEnd: false
    }),
    computed: {
        quizState() {
            return this.$store.state.quizState;
        },
        currentTestStep() {
            return this.$store.state.testStep;
        },
        currentQuestion (){
            return this.$store.state.questions[this.currentTestStep];
        },
        rightAnswer (){
            return this.currentQuestion.right;
        },
        testResult () {
            return this.$store.state.testResult;
        }
    },
    methods: {
        addAnswer(answer) {
            this.answer = answer;
            if(answer == this.rightAnswer) {
                this.$store.state.testResult++;
                this.resultAnswer = 'right';
            } else {
                this.resultAnswer = 'bad';
            }
            setTimeout(function() { 

                this.isResultAnswerWindow = true;
                this.answer = 0;
                }.bind(this), 500);
            
        },
        nextStep() {
            if(this.currentTestStep == 5) {
                this.isTestEnd = true;
            } else {
                this.$store.state.testStep++; 
            }
            this.isResultAnswerWindow = false;
            this.isTimeEnd = false;
        },
        endTime(){
            this.resultAnswer = 'bad';
            this.isTimeEnd = true;
        }
    }
}
</script>

