<template>
    <div class="header">
        <div class="header__logo" @click="toHome">
            <img src="../../assets/header-logo.png">
        </div>
        <div class="choose-btns" v-if="isActiveService || isActiveProducts || activePage == 'simulateur'">
            <div class="choose-block choose-products"  @click="goToPage('products')" v-bind:class="{'active' : isActiveProducts, 'active-media' : simulatorStep == 1 && activePage == 'simulateur'}">
                <div class="choose-content">
                    {{product !== 'null' ? 'PRODUIT:' : 'CHOISIR' }}<br>
                    <span class="bolder">{{product !== 'null' ? product : 'SON PRODUIT'}}</span>
                </div>
                <div class="choose-arrow">
                    <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"  class="arrow" v-bind:class="{'active' : isActiveProducts}" viewBox="0 0 53 30"><defs><path id="a2xaa" d="M1022.25 1363.07l-23.32-23.3a3.16 3.16 0 0 1 0-4.5l.35-.34a3.17 3.17 0 0 1 4.49 0l20.72 20.7 20.72-20.7a3.17 3.17 0 0 1 4.49 0l.37.33a3.16 3.16 0 0 1 0 4.5l-23.32 23.3a3.17 3.17 0 0 1-4.5 0z"/></defs><g><g transform="translate(-998 -1334)"><use  xlink:href="#a2xaa"/></g></g></svg>
                </div>
            </div>
            <div class="choose-block choose-servise" @click="goToPage('services')" v-bind:class="{'active' : isActiveService, 'unactive' : simulatorStep == 1}">
                <div class="choose-content">
                    {{service !== 'null' ? 'OFFRE:' : 'CHOISIR' }}<br> 
                    <span class="bolder">{{service !== 'null' ? service : 'SON OFFRE DE SOUTIEN '}}</span>
                </div>
                <div class="choose-arrow">
                    <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" class="arrow" v-bind:class="{'active' : isActiveService}" viewBox="0 0 53 30"><defs><path id="a2xaa" d="M1022.25 1363.07l-23.32-23.3a3.16 3.16 0 0 1 0-4.5l.35-.34a3.17 3.17 0 0 1 4.49 0l20.72 20.7 20.72-20.7a3.17 3.17 0 0 1 4.49 0l.37.33a3.16 3.16 0 0 1 0 4.5l-23.32 23.3a3.17 3.17 0 0 1-4.5 0z"/></defs><g><g transform="translate(-998 -1334)"><use  xlink:href="#a2xaa"/></g></g></svg>
                </div>
            </div>
        </div>
        <div class="secret-btn" v-if="activePage == 'contacts'"  @click="goToPage('secret')"></div>
        <div class="secrets"  v-if="activePage == 'secret'">
            <div v-if="activePage == 'secret' && secretComponentState == 'moreInfo'" class="secret-button" @click="goToTable">Back</div>
            <div v-if="activePage == 'secret'"  @click="changeData" class="secret-button">Upgrade DATAS</div>
            <div v-if="activePage == 'secret'" class="secret-button" @click="updateContacts">Export Contacts</div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Header',
  computed: {
    activePage(){
        return this.$store.state.currentPage;
    },
    isActiveService(){
        if(this.$store.state.currentPage == "services"){
            return true;
        } else {
            return false;
        }
    },
    secretComponentState(){
        return this.$store.state.currentSecretComponent;
    },
    product(){
        return this.$store.state.product;
    },
    currentTestStep(){
        return this.$store.state.testStep;
    },
    service() {
        return this.$store.state.service;
    },
    isActiveProducts(){
        if(this.$store.state.currentPage == "products"){
            return true;
        } else {
            return false;
        }
    },
    simulatorStep: function() {
        return this.$store.state.simulatorStep;
    }
  },
  methods: {
      goToTable(){
          this.$store.state.currentSecretComponent = 'table';
      },
    updateContacts(){
        var xhr = new XMLHttpRequest();

        var users = window.localStorage.getItem('users')
        var body = JSON.stringify(users);
        xhr.open("POST", 'https://thales.mikmak.in/import', true);
        xhr.setRequestHeader('Access-Control-Allow-Origin', '*');
            xhr.setRequestHeader('X-CSRF-TOKEN', 'NaryiwmdRJNiNG1Am97rGR3ZnbxjV8E5mSUjaTqz');
            xhr.setRequestHeader('Content-type', 'application/json;');
            xhr.responseType = 'json';

        xhr.onreadystatechange = function() {
        };
        xhr.send(body);
        this.$store.state.isDonePopup = true;
    },
    changeData(){
        this.getAllDataInLocalStorage('ondemand', 'https://thales.mikmak.in/getJson/ondemand');     
        this.getAllDataInLocalStorage('managed', 'https://thales.mikmak.in/getJson/manageservices');  
        this.getAllDataInLocalStorage('packaged-spare', 'https://thales.mikmak.in/getJson/stock');  
        this.getAllDataInLocalStorage('packaged-upgrade', 'https://thales.mikmak.in/getJson/upgrade');  
        this.getAllDataInLocalStorage('supported', 'https://thales.mikmak.in/getJson/support'); 
        this.$store.state.isDonePopup = true; 

    },
    getAllDataInLocalStorage(item, url) {
            var request = new XMLHttpRequest();
            request.open('GET', url, true);
            request.setRequestHeader('Access-Control-Allow-Origin', '*');
            request.setRequestHeader('X-CSRF-TOKEN', 'NaryiwmdRJNiNG1Am97rGR3ZnbxjV8E5mSUjaTqz');
            request.setRequestHeader('Content-type', 'application/jsonp;');
            request.responseType = 'json';
            request.send();
            request.onload = (function() {
                window.localStorage.setItem(item, JSON.stringify(request.response));
            }).bind(this)
        },
    toHome() {
        if(this.activePage == "simulateur") {
            this.$store.state.isPopupToHome = true;
        } else if(this.activePage == "quiz" && this.currentTestStep < 5){
            this.$store.state.isQuizPopup = true,
            this.$store.state.pageForPopup = 'home',
            this.$store.state.quizState = 'preload';
            this.$store.state.testStep = 0;
            this.$store.state.testResult = 0;
            this.$store.state.simulatorStep = 1;
            this.$store.state.activeNavigate = '';
            this.$store.state.product = 'null';
            this.$store.state.service = 'null';
        }
        else {
            this.$store.state.currentPage = 'home';
            this.$store.state.product = 'null';
            this.$store.state.service = 'null';
        }
    },
    goToPage(page) {
        if (page == 'services') {
            if(this.simulatorStep !== 1) {
                this.$store.state.currentPage = page;
            }
        } else {
           this.$store.state.currentPage = page;
        }
    }
  }
}
</script>


