<template>
    <div>
    <div class="graph">
        <svg class="line" > 
            <polyline v-bind:points="coordinates" v-bind:stroke="strokeColor" stroke-width="4" fill="transparent" v-bind:stroke-dasharray="dashArray"/>
        </svg>
    </div>
    </div>
</template>

<script>

export default {
    name: 'GraphLine',
    props: {
      years: Array,
      color: String,
      isDasharay: Boolean
    },
    computed: {
        dashArray:  function() {
            return this.isDasharay ? '5, 10' : '0, 0';
        },
        strokeColor: function(){
            if (this.color == 'first'){
                return '#5dbfd4';
            } else if (this.color == 'second') {
                return '#5dbfd4';
            } else if (this.color == 'theard') {
                return '#8ce320';
            } else if (this.color == 'fourth') {
                return '#8ce320';
            }
        },
        coordinates: function() {
            var pointStepX = (1253 / 30 ).toFixed();
            var coordinatesArr = '';
            for (var i = 1; i < 31; i++) {
                if(i == 1){
                    coordinatesArr = coordinatesArr + '0' + ',' + (400 - this.years[i-1]*200) + ' ';
                }else {
                    coordinatesArr = coordinatesArr + ((i-1)*pointStepX) + ',' + (400 - this.years[i-1]*200) + ' ';
                }
            }  
            return coordinatesArr;
        }
    }

}
</script>

<style scoped>
.graph {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.line {
    width: 100%;
    height: 400px;
}

</style>
