<template>
    <div class="timer" v-bind:class="{'add-margin': testStep == 1 || testStep == 4, 'add-margin2': testStep == 1 }">
        <div class="time-line"></div>
    </div>
</template>

<script>

export default {
    name: 'Timer',
    computed: {
        testStep: function () {
            return this.$store.state.testStep;
        },      
    },
    props: {
        endTime: Object
    },
    data: () => ({
        timer: null
    }),
    created: function(){
        this.createTimer();
    },
    destroyed: function(){
        this.clearTimerDestroy();
    },
    methods: {
        timerFunc(){
            this.endTime();
            clearTimeout(this.timer);
            this.timer = null;
        },
        createTimer(){
            this.timer = window.setTimeout(this.timerFunc.bind(this), 30000);
        },
        clearTimerDestroy(){
            clearTimeout(this.timer);
            this.timer = null;
        }
    }
}
</script>

