                <div class="right">
                    <input requred type="test" class="form-input" v-bind:class="{'error' : isErrorNom}" placeholder="Nom" id="nom" name="nom" v-model="nom">
                    <input requred type="test" class="form-input" v-bind:class="{'error' : isErrorPrenom}" placeholder="Prénom" id="prenom" name="prenom" v-model="prenom">
                    <input requred type="email" class="form-input" v-bind:class="{'error' : isErrorEmail}" placeholder="Adresse email" id="email" name="email" v-model="email">
                    <input requred type="test" class="form-input" v-bind:class="{'error' : isErrorLentitle}" placeholder="Nom de l’entité" id="nomDe" name="nomDe" v-model="nomDe">
                    <input requred type="test" class="form-input" v-bind:class="{'error' : isErrorPays}" placeholder="Pays" id="pays" name="pays" v-model="pays">
