          <div class="popup-legale-close"  @click="showLegalePopup()">&#215;</div>La protection de vos données à caractère personnel est d’une grande importance pour THALES. C’est pourquoi THALES prend toutes les mesures raisonnables pour<br> s’assurer que vos données à caractère personnel sont traitées en toute sécurité.<br>

