<script>

export default {
    name: 'Contacts',
    data: () => ({
        first: '',
        second: '',
        theard: '',
        nom: '',
        prenom: '',
        email: '',
        nomDe: '',
        pays: '',
        info: '',
        isErrorFirst: false,
        isErrorSecond: false,
        isErrorTheard: false,
        isErrorNom: false,
        isErrorPrenom: false,
        isErrorEmail: false,
        isErrorLentitle: false,
        isErrorPays: false,
        isPopup: false,
        isLegalePopup: false

    }),
    destroyed: function(){
        this.clearTimerDestroy();
    },
    computed: {
        legalePopup: function(){
            return this.isLegalePopup;
        }
    },
    methods: {
        showLegalePopup: function(){
            this.isLegalePopup = !this.isLegalePopup;
        },
        validateForm: function() {
            if(this.first == '') {
                this.isErrorFirst = true;
            } else {
                this.isErrorFirst = false;
            }
            if(this.second == '' || this.second == 'false') {
                this.isErrorSecond = true;
            } else {
                this.isErrorSecond = false;
            }
            if(this.nom == ''){
                this.isErrorNom = true;
            } else {
                this.isErrorNom = false;
            }
            if(this.prenom == ''){
                this.isErrorPrenom = true;
            }else {
                this.isErrorPrenom = false;
            }
            if(this.email == ''){
                 this.isErrorEmail = true;
            } else {
                this.isErrorEmail = false;
            }
            if(this.nomDe == ''){
                this.isErrorLentitle = true;
            } else {
                this.isErrorLentitle = false;
            }
            if(this.pays == ''){
                this.isErrorPays = true;
            } else {
                this.isErrorPays = false;
            }

            if(!this.isErrorFirst && !this.isErrorSecond  && !this.isErrorNom && !this.isErrorPrenom && !this.isErrorEmail && !this.isErrorLentitle && !this.isErrorPays) {
                var data = window.localStorage.getItem('users');
                var users;
                if(data === null) {
                    users = [];
                } else {
                    users =  JSON.parse(data);
                }
                var now = new Date();
                var prod = window.localStorage.getItem('product');
                var service = window.localStorage.getItem('service');
                var newUser = {
                    id: users.length,
                    chooseProduct: prod,
                    chooseServise: service,
                    nom: this.nom,
                    prenom: this.prenom,
                    email: this.email,
                    nomDe: this.nomDe,
                    pays: this.pays,
                    info: this.info,
                    data: now,
                    q1: {
                        pas_intéressé : false,
                        peu_intéressé : false,
                        plutôt_intéressé : false,
                        intéressé : false,
                        très_intéressé : false
                    },
                    q2: {
                        on_demand: false,
                        package_services : false,
                        support_to_operation : false,
                        managed_services : false
                    },
                    q3: {
                        pas_du_tout_urgent : false,
                        peu_urgent : false,
                        assez_urgent : false,
                        urgent : false,
                        très_urgent : false
                    },
                    q4: {
                        pas_du_tout_urgent : false,
                        peu_urgent : false,
                        assez_urgent : false,
                        urgent : false,
                        très_urgent : false
                    },
                    q5: {
                        inexistant : false,
                        faible : false,
                        moyen : false,
                        elevé : false,
                        très_élevé : false
                    },
                    q10: {
                        défense  : false,
                        civil : false
                    },
                    q11: {
                        aérien: false,
                        terrestre: false,
                        naval: false
                    },
                    q12: {
                        r1: false,
                        r2: false,
                        r3: false,
                        r4: false,
                        r5: false
                    },
                    comment: ''
                };
                users.push(newUser);
                window.localStorage.setItem('users', JSON.stringify(users)); 
                this.isPopup = true;
                this.createTimer();

            }
        },
        timerFunc(){
            this.$store.state.currentPage = "home";
            clearTimeout(this.timer);
            this.timer = null;
        },
        createTimer(){
            this.timer = window.setTimeout(this.timerFunc.bind(this), 3000);
        },
        clearTimerDestroy(){
            clearTimeout(this.timer);
            this.timer = null;
        }
    }
}
</script>

