<template>
    <div class="window">
        <div class="title">
            <div v-if="testResult == 'right'">
                <img src="../../assets/win.png">
            </div>
            <div v-if="testResult == 'bad'">
                <img src="../../assets/unwin.png">
            </div>
            <div class="radar">
                <img src="../../assets/small-radar.png">
            </div>
        </div>
        <div class="subtitle" v-if="testResult == 'right'">Très bien ! </div>
        <div class="subtitle" v-if="testResult == 'bad'">Bien essayé</div>
        <div class="podtitle" v-if="testResult == 'right'">La pérennité des systèmes n’a pas de secret pour vous !</div>
        <div class="podtitle" v-if="testResult == 'bad'">Néanmoins, vous avez pris connaissance de nouveaux éléments qui vont vous aider pour pérenniser vos systèmes.</div>
        <div class="message" v-if="testResult == 'right'">
            <p>Vous l’avez compris les obsolescences et les pertes de compétences sont inévitables.  Pour vous accompagner, Thales met à votre disposition 3 niveaux de services.</p>
            <p>Grâce à notre démonstrateur, apprenez-en davantage sur nos différents niveaux d’offres de services et l’impact sur votre coût de possession.</p>
            <!-- <div class="start-btn" @click="goToSimulator">c’est parti !</div> -->
        </div>
        <div class="message" v-if="testResult == 'bad'">
            <p> Les obsolescences et les pertes de compétences sont inévitables.  Pour vous accompagner, Thales met à votre disposition 3 niveaux de services.</p>            
            <p>Grâce à notre démonstrateur, apprenez-en davantage sur nos différents niveaux d’offres de services et l’impact sur votre coût de possession.</p>
            <!-- <div class="start-btn" @click="goToLibrary">c’est parti !</div> -->
        </div>
    </div>
</template>

<script>

export default {
    name: 'TestResult',
    props: {
        result: Number,
    },
    created: function(){
        setTimeout(function() { 
            if(this.testResult == 'right'){
                this.$store.state.activeNavigate = 'simolator';
            } else {
                this.$store.state.activeNavigate = 'library';
            }
        }.bind(this), 100);

    },
    methods: {
        goToSimulator: function(){
            this.$store.state.currentPage = "simulateur";
            this.$store.state.quizState = 'preload';
            this.$store.state.testStep = 0;
            this.$store.state.testResult = 0;
            this.$store.state.simulatorStep = 1;
        },
        goToLibrary: function(){
            this.$store.state.currentPage = "bibliotheque";
            this.$store.state.quizState = 'preload';
            this.$store.state.testStep = 0;
            this.$store.state.testResult = 0;
            this.$store.state.simulatorStep = 1;
        }
    },
    computed: {
        testResult(){
            if(this.result > 4) {
                return 'right';
            } else {
                return 'bad';
            }
        }
    }
}
</script>

