<template>
    <div class="graphs">
        <div class="left">
            <div class="graph-title-left">à LA DEMANDE</div>
            <div class="graph-container-left">
                <GraphColumn v-bind:label="'COûT'" v-bind:size="demandCostSimulationValue" v-bind:color="'second'"/>
                <GraphColumn v-bind:label="'Disponibilité'" v-bind:size="demandSystemAvValue" v-bind:color="'fourd'" />
            </div>
        </div>
        <div class="right">
            <div class="graph-title-right">{{service}}</div>
            <div class="graph-container-right">
                <div class="graph-container-right">
                    <GraphColumn v-bind:label="'COûT'" v-bind:size="currentCostSimulationValue" v-bind:color="'second'"/>
                    <GraphColumn v-bind:label="'Disponibilité'" v-bind:size="currentSystemAvValue" v-bind:color="'fourd'" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import GraphColumn from './GraphColumn.vue';

export default {
    name: 'GraphsColumns',
    components: {
        GraphColumn
    },
    data: ()=>({
        demandData: [],
        currentData: [],
        activeGraph: []

    }),

    created: function(){
        this.getDemandeGraph();
        this.getCurrentGraph();
    },
    computed: {
        service(){
            return this.$store.state.service;
        },
        year: function() {
            return this.$store.state.currentYear;
        },
        graph: function(){
            return this.$store.state.simulatorGraph;
        },
        demandCostSimulationObject: function(){
            if(this.demandData){
                return this.demandData[0];
            }        
        },
        demandCostSimulationValue: function(){
            if(this.demandCostSimulationObject){
                return this.demandCostSimulationObject[this.year];
            }
        },
        demandSystemAvObject: function(){
            if(this.demandData){
                return this.demandData[2];
            }
        },
        demandSystemAvValue: function(){
            if(this.demandSystemAvObject){
                return this.demandSystemAvObject[this.year];
            }
        },
        currentGraph: function(){
            return this.$store.state.simulatorGraph;
        },
        currentGraphTitle: function(){
            if(this.$store.state.simulatorGraph == 'packaged-spare' || this.$store.state.simulatorGraph == 'packaged-upgrade'){
                return 'Sur mesure';
            } else if (this.$store.state.simulatorGraph == 'managed') {
                return 'Support au fonctionnement';
            } else if (this.$store.state.simulatorGraph == 'supported'){
                return 'Support à l’exploitation';
            }
        },
        currentCostSimulationObject: function(){
            if (this.currentData) {
                return this.currentData[0];
            }
        },
        currentCostSimulationValue: function(){
            if(this.currentCostSimulationObject){
                return this.currentCostSimulationObject[this.year];
            }
        },
        currentSystemAvObject: function(){
            if (this.currentData){
                return this.currentData[2];
            }
        },
        currentSystemAvValue: function(){
            if(this.currentSystemAvObject){
                return this.currentSystemAvObject[this.year];
            }
        },
    },
    methods: {
        getDemandeGraph: function(){
            var data = window.localStorage.getItem('ondemand');
            this.demandData =  JSON.parse(data);
        },
        getCurrentGraph: function(){
            var data = window.localStorage.getItem(this.currentGraph)
            this.currentData = JSON.parse(data);
        }
    }
}
</script>

