<template>
    <div class="backgs">
        <div class="bg-window bg-1" v-bind:class="{'active' : currentBg == 1}"></div>
        <div class="bg-window bg-2" v-bind:class="{'active' : currentBg ==2}"></div>
        <div class="bg-window bg-3" v-bind:class="{'active' : currentBg ==3}"></div>
        <div class="bg-window bg-4" v-bind:class="{'active' : currentBg ==4}"></div>
        <div class="bg-window bg-5" v-bind:class="{'active' : currentBg ==5}"></div>
        <div class="bg-window bg-6" v-bind:class="{'active' : currentBg ==6}"></div>
    </div>
</template>

<script>

export default {
    name: 'Background',
    data: () => ({
        currentBg: 1
    }),
    created : function(){
        this.changeCurrentBg()     
    },
    methods: {
        changeCurrentBg : function(){          
            this.currentStep = setInterval(function(){
                if(this.currentBg == 6){
                    this.currentBg = 1;
                    this.currentStep = this.currentBg;
                    return this.currentStep;
                }else{
                    this.currentBg++
                    this.currentStep = this.currentBg;
                    return this.currentStep;}
            }.bind(this), 3000);
        },
    }
}
</script>

